* {
    margin: 0;
    padding: 0%;
    box-sizing: border-box;
}

#root {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.App {
    width: 800px;
}

.navbar {
    width: 100%;
    height: 50px;
    font-size: 24px;
    display: flex;
    justify-content: center;
    background-color: #5F9EA0;
    align-items: center; 
}

p {
    margin: 0px 30px;
    color: black;
    font-size: 24px;
    font-weight: 900;
}