.wrapper {
    margin: 15px 0;
    width: 50%;
    height: 100%;
    border: 6px solid lightsteelblue;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.card {
    width: 15%;
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 2px solid #C9E4F6;
    margin: 10px 0;
}