* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

::-webkit-scrollbar {
    display: none;
}

.main {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.btn-list {
    display: flex;
    justify-content: space-between;
}

.Table {
    margin-top: 15px;
    width: 50%;
    height: 100%;
    border: 3px solid lightsteelblue;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin: 0px auto;   
}

thead {
    background-color: rgb(145, 181, 212);
}

tr {
    transition: 0.5s;
}

tr:nth-child(even) {
    background-color: #C9E4F6;
}



tr:hover {
    background-color: #82c1e9;
}

th, td {
    border: solid 3px lightsteelblue;
    padding: 5px;
}

p {
    font-size: 18px;
    font-weight: 200;
}